/*--------------------------------------------------------------
# Hero Section
--------------------------------------------------------------*/
#heroo {
  width: 100%;
  height: 90vh;
  background-size: cover;
  position: relative;
}

#heroo:before {
  content: "";
  background: rgba(0, 0, 0, 0.6);
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
}

#heroo .container {
  position: relative;
  padding-top: 74px;
  text-align: center;
}

#heroo h1 {
  margin: 0;
  font-size: 56px;
  font-weight: 700;
  line-height: 64px;
  color: #fff;
  font-family: "Poppins", sans-serif;
}

#heroo h1 span {
  color: #15c2e0;
}

#heroo h2 {
  color: rgba(255, 255, 255, 0.9);
  margin: 10px 0 0 0;
  font-size: 24px;
}

#heroo .icon-box {
  padding: 30px 20px;
  transition: ease-in-out 0.3s;
  border: 1px solid rgba(255, 255, 255, 0.3);
  height: 100%;
  text-align: center;
}

#heroo .icon-box i {
  font-size: 32px;
  line-height: 1;
  color: #15c2e0;
}

#heroo .icon-box h3 {
  font-weight: 700;
  margin: 10px 0 0 0;
  padding: 0;
  line-height: 1;
  font-size: 20px;
  line-height: 26px;
  color: #fff;
}
#heroo .icon-box h3:hover {
  color: #15c2e0;
}

#heroo .icon-box h3 a {
  color: #fff;
  transition: ease-in-out 0.3s;
}

#heroo .icon-box h3 a:hover {
  color: #15c2e0;
}

#heroo .icon-box:hover {
  border-color: #15c2e0;
}

@media (min-width: 1024px) {
  #heroo {
    background-attachment: fixed;
  }
}

@media (max-width: 768px) {
  #heroo {
    height: auto;
  }
  #heroo h1 {
    font-size: 28px;
    line-height: 36px;
  }
  #heroo h2 {
    font-size: 20px;
    line-height: 24px;
  }
}
/*--------------------------------------------------------------
# About
--------------------------------------------------------------*/
.about .content h3 {
  font-weight: 700;
  font-size: 28px;
  font-family: "Poppins", sans-serif;
}

.about .content ul {
  list-style: none;
  padding: 0;
}

.about .content ul li {
  padding: 0 0 8px 26px;
  position: relative;
}

.about .content ul i {
  position: relative;
  font-size: 20px;
  left: 0;
  top: -3px;
  color: #15c2e0;
}

.about .content p:last-child {
  margin-bottom: 0;
}
/*--------------------------------------------------------------
# Features
--------------------------------------------------------------*/
.features .icon-box {
  padding-left: 15px;
}

.features .icon-box h4 {
  font-size: 20px;
  font-weight: 700;
  margin: 5px 0 10px 60px;
}

.features .icon-box i {
  font-size: 48px;
  float: left;
  color: #15c2e0;
}

.features .icon-box p {
  font-size: 15px;
  color: #848484;
  margin-left: 60px;
}

.features .image {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 400px;
}
/*--------------------------------------------------------------
# Cta
--------------------------------------------------------------*/
.cta {
  background: linear-gradient(rgba(2, 2, 2, 0.5), rgba(0, 0, 0, 0.5));
  background-size: cover;
  padding: 60px 0;
}

.cta h3 {
  color: rgb(48, 117, 126);
  font-size: 28px;
  font-weight: 700;
}

.cta p {
  color: rgb(48, 117, 126);
}

.cta .cta-btn {
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 8px 28px;
  border-radius: 4px;
  transition: 0.5s;
  margin-top: 10px;
  border: 2px solid #fff;
  color: #fff;
  background-color: #0638413a;
}
.cta .cta-btn a {
  color: #fff;
}

.cta .cta-btn:hover {
  background: #15c2e0;
  border-color: #15c2e0;
  color: #151515;
}
