/*--------------------------------------------------------------
# General
--------------------------------------------------------------*/
body {
  font-family: "Open Sans", sans-serif;
  color: #0b6161;
  text-align: justify;
}

a {
  color: #2dadc1;
  text-decoration: none;
}

